import { Component, OnDestroy, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Select, Store } from '@ngxs/store';
import { Location } from '@angular/common';
import { UtilsService } from '../@core/utils.service';
import { BasePage } from '../base/base.page';
import { NetworkState } from '../@store/network/network.state';
import { Observable, Subscription } from 'rxjs';
import { UserPublicInfo } from 'src/API';
import { GetProfileVisitsAction } from '../@store/network/network.actions';

const MAX_VISITS_ITEMS = 10;
const DAYS = 7;

@Component({
  selector: 'app-profile-visits',
  templateUrl: './profile-visits.page.html',
  styleUrls: ['./profile-visits.page.scss'],
})
export class ProfileVisitsPage extends BasePage implements OnInit, OnDestroy {
  @Select(NetworkState.getVisits) profileVisitsList$: Observable<UserPublicInfo[]>;
  public maxVisits = MAX_VISITS_ITEMS;
  public param = { days: DAYS };

  constructor(router: Router, location: Location, store: Store, utilsService: UtilsService) {
    super(location, router, store, utilsService);
  }

  ngOnInit() {}

  ionViewWillEnter() {
    this.store.dispatch(new GetProfileVisitsAction({ days: DAYS }));
  }

  ngOnDestroy() {}
}
